import * as yup from 'yup'
import { FILE_SIZE, PHONE_REG_EXP, TELEGRAM_REG_EXP } from '../constants'
import {
  birthDateOptions,
  cityOptions,
  cohortNumberOptions,
  courseOptions,
  educationLevelOptions,
  experienceTypeOptions,
  firstNameOptions,
  genderOptions,
  imageOptions,
  institutionOptions,
  lastNameOptions,
  previousJobPositionOptions,
  urlOption,
} from '../schemas'

const telegramOption = yup
  .string()
  .matches(TELEGRAM_REG_EXP, '  a-z,  0-9,   ')
  .trim()

class OnboardingSchema {
  static readonly onboardingPersonalSchema = yup.object().shape({
    image: imageOptions.test(
      'fileSize',
      `   ${FILE_SIZE / 1048576} MB`,
      (value) => !value || (value && value.size <= FILE_SIZE),
    ),
    first_name: firstNameOptions,
    last_name: lastNameOptions,
    city: cityOptions,
    birth_date: birthDateOptions,
    gender: genderOptions,
  })

  static readonly onboardingExperienceSchema = yup.object().shape({
    course_id: courseOptions,
    cohort_number: cohortNumberOptions,
    experience_type: experienceTypeOptions,
    previous_job_position: previousJobPositionOptions,
    education_type: educationLevelOptions,
    institution: institutionOptions,
  })

  static readonly onboardingResumeSchema = yup.object().shape({
    portfolio_url: urlOption,
  })

  static readonly onboardingContactsSchema = yup.object().shape({
    telegram: telegramOption,
    email: yup.string().email('    ').nullable(true).trim(),
    whatsapp: yup.string().matches(PHONE_REG_EXP, '   '),
  })
}

export default OnboardingSchema
