/* eslint-disable no-unused-vars */
import React, { useEffect, useMemo, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { useForm, Controller, SubmitHandler } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { TextField, Button, FormControl, FormHelperText, Typography } from '@material-ui/core'
import OnboardingSchema from '../../../../utils/validation-schemas/onboarding-schemas'
import Dropzone from '../../../Sub/Dropzone/Dropzone'
import useStyles from './OnboardingFormResume.styles'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { ResumeInfo } from '../../../../services/types/onboarding/onboarding-types'

const prepareDataToStorage = (formData: ResumeInfo) => {
  const newResumeInfo = { ...formData, ...formData }
  delete newResumeInfo.files
  return JSON.stringify(newResumeInfo)
}

const OnboardingFormResume = () => {
  const classes = useStyles()
  const navigate = useNavigate()
  const location = useLocation()
  const dispatch = useAppDispatch()

  const avatar = location.state?.avatar
  const { status, isError } = useAppSelector((store) => store.onboarding)
  const personalInfo = JSON.parse(localStorage.getItem('personalInfo') as string)
  const experienceInfo = JSON.parse(localStorage.getItem('experienceInfo') as string)
  const resumeInfo = JSON.parse(localStorage.getItem('resumeInfo') as string)

  const { profile } = useAppSelector((store) => store.auth)

  const [files, setFiles] = useState<File[]>([])

  const defaultValues = resumeInfo || {
    portfolio_url: '',
    files: [],
  }

  const {
    control,
    handleSubmit,
    register,
    setValue,
    watch,
    getValues,
    formState: { errors },
  } = useForm<ResumeInfo>({
    defaultValues,
    resolver: yupResolver(OnboardingSchema.onboardingResumeSchema),
    mode: 'onChange',
  })

  useEffect(() => {
    if (!personalInfo) {
      navigate('/onboarding/personal-info')
    }
  }, [personalInfo])

  useEffect(() => {
    if (profile) {
      localStorage.removeItem('personalInfo')
      localStorage.removeItem('experienceInfo')
      localStorage.removeItem('projectsInfo')
      localStorage.removeItem('resumeInfo')
      localStorage.removeItem('contactsInfo')
      navigate('/diary')
    }
  }, [profile])

  useEffect(() => {
    setValue('files', files, { shouldValidate: true, shouldDirty: true })
  }, [files])

  const onSubmit: SubmitHandler<ResumeInfo> = (formData) => {
    localStorage.setItem('resumeInfo', prepareDataToStorage(formData))
    navigate('/onboarding/contacts', { state: { avatar, files } })
  }

  return (
    <>
      {isError && <Typography color="error">-   ,     </Typography>}
      <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        <fieldset className={classes.fieldset}>
          <div className={classes.fieldContainer}>
            <Dropzone filesState={[files, setFiles]} />
            <FormControl className={classes.input}>
              <FormHelperText>
                {errors?.files?.map((error, index) => `${error.message}` || '').join('\n') || ''}
              </FormHelperText>
            </FormControl>
          </div>
          <div className={classes.fieldContainer}>
            <Typography variant="body2">   ()</Typography>
            <Controller
              name="portfolio_url"
              control={control}
              render={({ field }) => (
                <TextField
                  error={!!errors.portfolio_url?.message}
                  helperText={errors.portfolio_url?.message || ' '}
                  defaultValue={resumeInfo?.portfolio_url}
                  {...field}
                  className={classes.input}
                  variant="outlined"
                  placeholder=" "
                />
              )}
            />
          </div>

          <div className={classes.buttonContainer}>
            <Button
              type="button"
              className={`${classes.formButton} ${classes.previousButton}`}
              onClick={() => {
                const values = getValues()
                localStorage.setItem('resumeInfo', prepareDataToStorage(values))
                navigate('/onboarding/projects', { state: { avatar } })
              }}
            >
              
            </Button>
            <Button className={classes.formButton} type="submit">
              
            </Button>
          </div>
        </fieldset>
      </form>
    </>
  )
}

export default OnboardingFormResume
